import React, {useState} from 'react';

// utils
import axios from 'axios';

//styles
import './style.css';
import './media.css';

const Registration = () => {
  const [stateSubmit, setStateSubmit] = useState(`*     .       .`)

  const [state, setState] = useState({
    name: '',
    phoneNumber: '',
  });

  const onChangeInput = (event) => {
    const { name, value } = event.target;
    setState((prev) => ({ ...prev, [name]: value }));
  };

  const fetchAddUserAction = async (e) => {
    e.preventDefault();
    const url = 'http://localhost:8080/api/user';
    await axios.post(url, { ...state }).then((res) => {
      console.log(res);
      setStateSubmit("*   *");
    });
    setState({ name: '', phoneNumber: '' });
  };

  return (
    <section className="registration" id="registration">
      <div className="formBlock anim-items">
        <div className="wannaWatch">
          <span> ?</span>
          <p>
            Lorem Ipsum -  -"",     
            -. Lorem Ipsum   ""  
                XVI .     
                   ,
             Lorem Ipsum   .
          </p>
        </div>

        <div className="input">
          <form id="form">
            <div id="formRaw1">
              <input
                id="nameInput"
                type="text"
                value={state.name}
                name="name"
                onChange={onChangeInput}
                placeholder=" "
              />
              <input
                id="phoneInput"
                type="text"
                value={state.phoneNumber}
                name="phoneNumber"
                onChange={onChangeInput}
                placeholder=" "
              />
            </div>

            <div id="formRaw2">
              <p>
                <small>
                  {stateSubmit}
                </small>
              </p>

              <button
                type="button"
                id="button"
                onClick={fetchAddUserAction}
              >
                 
              </button>
            </div>
          </form>
        </div>
      </div>
    </section>
  );
};

export default Registration;