import React from 'react';

// styles
import './style.css';
import './media.css';

// images
import mouseIcon from '../../assets/svg_icons/mouse.svg';

function TopBlock() {
  return (
    <section className="topBlock" id="#topAddress">

      {/*    */}
      <div className="header anim-items">
        <h1> <br/>   </h1>
      </div>

      {/*  */}
      <div id="mouseBlock">
        <img id="mouse" src={mouseIcon} />
      </div>
    </section>
  );
}

export default TopBlock;